import clientPromise from "../mongodb";
import { ObjectId } from "mongodb";

export interface User {
  _id?: ObjectId;
  googleId: string;
  email: string;
  name: string;
  picture?: string;
  emailVerified?: boolean;
  createdAt: Date;
  updatedAt: Date;
  lastLoginAt?: Date;
}

export interface CreateUserInput {
  googleId: string;
  email: string;
  name: string;
  picture?: string;
  emailVerified?: boolean;
}

async function getDatabase() {
  const client = await clientPromise;
  return client.db();
}

/**
 * Find user by Google ID
 */
export async function findUserByGoogleId(
  googleId: string
): Promise<User | null> {
  const db = await getDatabase();
  const user = await db.collection<User>("users").findOne({ googleId });
  return user;
}

/**
 * Find or create user by Google ID
 * If user exists, updates lastLoginAt and user info
 * If not, creates new user
 */
export async function findOrCreateUser(input: CreateUserInput): Promise<User> {
  const db = await getDatabase();
  const existingUser = await findUserByGoogleId(input.googleId);

  if (existingUser) {
    // Update last login time and user info
    const updateResult = await db.collection<User>("users").findOneAndUpdate(
      { _id: existingUser._id },
      {
        $set: {
          name: input.name,
          picture: input.picture,
          emailVerified: input.emailVerified,
          lastLoginAt: new Date(),
          updatedAt: new Date(),
        },
      },
      { returnDocument: "after" }
    );

    if (!updateResult) {
      throw new Error("Failed to update user in database");
    }

    return updateResult;
  }

  // Create new user
  const now = new Date();
  const newUser: Omit<User, "_id"> = {
    ...input,
    emailVerified: input.emailVerified ?? false,
    createdAt: now,
    updatedAt: now,
    lastLoginAt: now,
  };

  const result = await db.collection<User>("users").insertOne(newUser as User);

  const user = await db.collection<User>("users").findOne({
    _id: result.insertedId,
  });

  if (!user) {
    throw new Error("Failed to create user");
  }

  return user;
}
